.choice-container {
    text-align: left;
    border: 1px solid #ccc;
    position: relative;
    border-radius: 6px;
    cursor: pointer;
    width: max-content;
}

.choice-container .rotArrow {
    transform: rotate(-180deg);
    transition: all 400ms ease-in-out;
} 

.choice-container .normArrow {
    transform: rotate(0);
    transition: all 400ms ease-in-out;
}

.choice-container .dropdown-input {
    padding: 7px 11px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    user-select: none;
    gap: 7px;
}

.choice-container .dropdown-menu {
    width: max-content;
    padding: 5px;
    position: absolute;
    border: 1px solid #bbbbbb;
    border-radius: 6px;
    overflow: auto;
    background-color: #fcfcfc;
    z-index: 99;
    max-height: 500px;
    min-height: 50px;
    transform: translateY((-10px));
    transition: 400ms ease-in-out;
}

.choice-container .dropdown-menu.alignment--left {
    left: 0;
}

.choice-container .dropdown-menu.alignment--right {
    right: 0;
}

.choice-container .dropdown-item {
    padding: 7px 10px;
    cursor: pointer;
    transition: all 0.35s ease;
    border-radius: 6px;
    font-weight: 500;
    display: flex; 
    flex-direction:column;
}

.choice-container .dropdown-item:hover {
    background-color: rgba(190, 190, 255, 0.4);
    color: #738f00;
}

.choice-container .dropdown-item.selected {
    background-color: rgba(255, 115, 0, 0.075);
    color: #ff2020;
    font-weight: 600;
    display: flex; 
    flex-direction:column;
}

.description {
    margin-top: 0.2em;
    font-weight: 200;
    font-size: 0.8rem;
    padding: 0.5em;
    line-height: 1.4rem;
    background-color: rgba(255, 255, 255, 0.085);
    color: #555;
}

.dropdown-item:nth-child(even){
    background-color: #f8f8f8;
}

.dropdown-item:nth-child(odd){
    background-color: #eaffff;
}

.dropdown-disabled {
    font-weight: 200;
    color: #82868b;
}

.dropdown-placeholder {
    font-weight: 200;
    color: #82868b;
}

.dropdown-selected {
    font-weight: 600;
    color: #222;
}