.iconButton {
    border: none;
    overflow: none;
    padding: 0;
    cursor: pointer;
}

.iconButton:hover {
    background-color: rgba(190, 190, 255, 0.6);
    border: 2 solid #77d;
    border-radius: 5px;
}

.svgSpan {
    cursor: pointer;
}

.svgSpan:hover {
    background-color: rgba(190, 190, 255, 0.6);
    border: 2 solid #77d;
    border-radius: 5px;
}
