.abutton
  {
    height: 30px;
    background-color:#943;
    display: inline-block;
    padding:2px;
    color:white;
    border: 2px solid #510;
    font-family: sans-serif;
    font-size: 14px;
    border-radius: 4px;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
  }

.abutton:hover
  {
    background-color:white;
    color:#620;
    border: 2px solid #300;
  }  
