.react-select-card {
    display: inline-block;
    font-size: 1.0em;
    width:54px;
    height:40px;
    color:#040;
    margin:0.1em 0.4em 0.1em 0.4em;
    background-color: #830;
  }
  
  /* Remove border, outline, box-shadow, and min-height values */
  .react-select-card .react-select__control {
    border: none;
    outline: none;
    box-shadow: none;
    min-height: none;
    cursor: pointer;
    color:#040;
    background-color: #830;
  }
  
  /* Tighten up spacing */
  .react-select-card .react-select__value-container {
    padding: 0;
  }
  
  /* Position value relative (instead of absolute) and remove transform and max-width */
  .react-select-card .react-select__single-value {
    color:#eff;
    background-color: #071;
    font-size: 1.1em;
    font-family:serif;
    position: relative;
    transform: none;
    max-width: none;
    padding: 0.2em 0.5em 0.2em 0.5em;
  }
  
  /* CSS triangle dropdown indicator next to selected value */
  .react-select-card .react-select__single-value::after {
    content: " ";
    position: relative;
    display: inline-block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 5px 4px 0 4px;
    border-color: #000000 transparent transparent transparent;
  
    margin-left: 0.25em;
    top: -0.125em;
  }
  
  .react-select-card .react-select__menu {
    width: 60px;
  }