.react-select-puzzle {
    display: inline-block;
    padding-bottom: 0.5em;
    font-size: 1.1em;
    width:90px;
  }
  
  /* Remove border, outline, box-shadow, and min-height values */
  .react-select-puzzle .react-select__control {
    border: none;
    outline: none;
    box-shadow: none;
    min-height: none;
    cursor: pointer;
    font-size: 1.0em;
  }
  
  /* Tighten up spacing */
  .react-select-puzzle .react-select__value-container {
    padding: 0;
  }
  
  /* Position value relative (instead of absolute) and remove transform and max-width */
  .react-select-puzzle .react-select__single-value {
    font-weight: bold;
    position: relative;
    transform: none;
    max-width: none;
    font-size: 1.0em;
    line-height: 70px;
  }
  
  /* CSS triangle dropdown indicator next to selected value */
  .react-select-puzzle .react-select__single-value::after {
    content: " ";
    position: relative;
    display: inline-block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 5px 4px 0 4px;
    line-height: 70px;
    border-color: #000000 transparent transparent transparent;
    font-size: 1.0em;
    margin-left: 0.5em;
    top: -0.125em;
  }
  
  .react-select-puzzle .react-select__menu {
    width: auto;
    font-size: 1.0em;
  }

  .react-select-puzzle .react-select__option {
    font-size: 1.0em;
  }

  .still {
    
  }
  

  .tile {
    transition: 1s;
  }
  
  .tile :hover {
    border-style: solid;
    border-width: 3px;
    border-color: #fd4;
  }