.custom {
    padding:0em;
    border: 8px solid #ccc;
    background-color: #ddd;
    width:490px;
    height:540px;
    border-radius: 5px;
}

.cbutton {
    padding: 0.1.5em 0.6em 0.2em 0.6em;
    margin: 0.3em 0.3em 0.25em 0.2em;
    font-size: 1.0em;
    cursor: pointer;
    border: 2.5px solid #04AA99;
    color: #154;
    background: #eeffff;
}

.cbutton:hover {
    background: #04AA99;
    color: white;
}

.cbutton:disabled, .cbutton[disabled] {
    border: 1.5px solid #09a;
    background-color: #e6ffe6;
    color: #467;
    opacity: 0.6;
    cursor: not-allowed;
}

.cselect {
    border: 1px solid #002;
    width: 38px;
    height: 25px;
    padding: 1px,0;
    font-size: 1.0rem;
    font-weight: 600;
    color:#833;
    background-color:#bbf;
}

.citem {
    border: 1px solid #111;
    width: 38px;
    height: 25px;
    padding: 1px,0;
    font-size: 0.9rem;
    font-weight: 400;
    color:#333;
    background-color: #eee;
    cursor: pointer;
}

.citem:hover {
    color:#eee;
    background-color: #333;
}

