.lose {
    padding:0em;
    border: 8px solid #B35724;
    background-color: #FED792;
    width:240px;
    height:160px;
}

.win {
    padding:0em;
    border: 8px solid #FE4E51;
    background-color: #dd846E;
    width:240px;
    height:160px;
}

.bar {
    margin-top:1.1em;
    text-align: center;
}

.msg {
    color:#111;
    margin-top:1.1em;
    font-size: 0.9em;
    text-align: center;
}

.ltitle {
    width:100%;
    height:32px;
    margin:0px;
    padding:0px;
    border:0;
    background-color: #B35724;
}

.wtitle {
    width:100%;
    height:32px;
    margin:0px;
    padding:0px;
    border:0;
    background-color: #FE4E51;
}


.mbtn {
    width:30px;
    height:30px;
    float: right;
    padding:2px;
    border:0;
    background-color:transparent;
}

.mbtn:hover {
    background: #FFE4BE;
}