.dlg {
    padding:0em;
    border: 8px solid #FE4E51;
    background-color: #dd846E;
    width:600px;
    height:460px;
}

.mbtn {
    width:28px;
    height:26px;
    margin-top:-2px;
    float: right;
    padding:1px;
    border:0;
    background-color:transparent;
}

.title {
    width:100%;
    height:28px;
    margin:0px;
    padding:0px;
    border:0;
    background-color: #FE4E51;
}

.preview {
    margin: 0.5rem;
}

.fields {
    margin: 0rem 0.5em 0.3em 0.5em;
}

.img-area {
    max-width: 500px;
    max-height: 300px;
}

.fieldName {
    font-family: sans-serif;
    font-weight: 600;
    font-size: 0.9rem;
    color:#333;
    width:50px;
    display: inline;
    padding: 0.4rem;
}

.fieldValue {
    font-family: serif;
    font-weight: 400;
    color:#222;
    display: inline;
    padding: 0.4rem;
}
